export default {
  async fetch() {
    this.global = await this.$strapi.find("global");
  },
  data: function () {
    return {
      global: [],
      showMobileMenu: false,
    };
  },
  watch: {
    $route() {
      this.showMobileMenu = false;
    },
  },
  methods: {
    showMenu() {
      this.$emit("showMobileMenu");
    },
    closeMenu() {
      this.$emit("closeMobileMenu");
    },
  },
};
